<template>
  <v-app dark>
    <!--The Side Menu component go here -->  
    <SideMenu :drawer="drawer"  :api_key="api_key" @selectsource="setResource" ></SideMenu>
    <v-app-bar fixed app clipped-left color="secondary" class="elevation-2">
      <v-app-bar-nav-icon @click="drawer = !drawer" class="white--text"></v-app-bar-nav-icon>
      <v-toolbar-title class="white--text">News App</v-toolbar-title>
    </v-app-bar>
    <v-main>
      <v-container fluid dark>
        <!--The Main Content component goes here--> 
        <MainContent :articles="articles"></MainContent>
      </v-container>
    </v-main>
    <v-footer class="secondary" app height="30px">
      <v-layout row wrap align-center>
        <v-flex xs12>
          <div class="white--text ml-3 text-center">
            Made by 
           <a class="white--text " href="https://github.com/ndenys650" target="_blank">Nate Denys</a>
          </div>
        </v-flex>
      </v-layout>
    </v-footer>
  </v-app>
</template>



<script>
import axios from 'axios' 
import MainContent from './components/MainContent.vue' 
import SideMenu from './components/SideMenu.vue' // import the SideMenu component

export default {
  components: {
    MainContent,
    SideMenu // Register the component
  },
  data () {
    return {
      drawer: false,
      api_key:'9d7f346a437b47ab9c5ce374153939f9', 
      articles: [],
      errors: [] 
    }
  },
  created () {
    axios.get('https://newsapi.org/v2/top-headlines?sources=techcrunch&apiKey='+this.api_key)
      .then(response => {
        this.articles = response.data.articles
        // console.log('data:')
        console.log(response.data.articles) // This will give you access to the full object
      })
      .catch(e => {
        this.errors.push(e)
      })
    },


    //add the methods events hadler with setResource() function
  methods: {
    setResource(source) {
      axios.get('https://newsapi.org/v2/top-headlines?sources='+source+'&apiKey='+this.api_key
      )
      .then(response => {
        this.articles = response.data.articles
        console.log(this.articles)             
      })
      .catch(e => {
        this.errors.push(e)
      })
    }
  }
}
</script>